<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
     
          <ul v-for="n in listnums"><li>
    {{n}}
          </li></ul>
          <button @click="zhanshi">加载数据</button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',listnums:[{lilei:8},{xiaoxin:5},{xiaokui:1},{tanghulu:9}]
    }
  },
  methods:{
   zhanshi:function(){
  this.$http.get("http://127.0.0.1/listbyid.php").then(function(data){
   this.listnums.concat(data.body);
  var ccc=this.listnums;
  ccc[0]["lilei"]=12;
  this.listnums=[{tom:33}]; 
  console.log(data);
  },function(errd){
  console.log(errd);
  });
console.log("zhanshilo");
 }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {color:green;
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



